import React, {useEffect} from 'react';
import CustomTextField from '../../../../Layout/CustomTextField';

function AbnNum({secF, onChangeFn, formErrors}) {

    useEffect(()=>{
        return ()=>{
            onChangeFn({name:'abn', value:''})
        }
    },[])

  return (
    <CustomTextField 
        placeholder="Trust Australian Business Number"
        id="sec-f-trust-aus-business-num"
        label="Trust Australian Business Number (ABN)"
        type="form-input-full-width"
        asterisk="*"
        name="abn"
        value={secF?.abn}
        onChangeFn={onChangeFn}
        formErrors={formErrors}
    />
  )
}

export default AbnNum