import React, {useEffect} from 'react';
import CustomTextField from '../../../../Layout/CustomTextField';

function OtherTrustIn({secF, onChangeFn, formErrors}) {

    useEffect(()=>{
        return ()=>{
            onChangeFn({name:'other_type_specification', value:''})
        }
    },[])

  return (
    <div>
        <CustomTextField
            placeholder=""
            id="sec-f-trust-type-other"
            label="Please specify"
            type="form-input-full-width"
            asterisk="*"
            name="other_type_specification"
            value={secF?.other_type_specification || ""}
            onChangeFn={onChangeFn}
            formErrors={formErrors}
        />
    </div>
  )
}

export default OtherTrustIn