import React, {useEffect} from 'react';
import CustomTextField from '../../../Layout/CustomTextField';
import { useSelector } from 'react-redux';

const BeneficiaryClasses = ({secF, onFormChange}) => {
  const invFormStatus = useSelector((store) => store.invFormStatus)

  useEffect(()=>{
    return ()=>{
      onChangeFn({name:'beneficiary_class', value:''})
    }
  },[])

  const onChangeFn = ({name, value}) =>{
    onFormChange({name, value});
  }

  return (
    <div className="section__container">
        <h4 className="section__container--subtitle">F.3 Beneficiary Classes</h4>

        <div className="section__container--box">
            <CustomTextField
              placeholder="Class(es) of beneficiaries"
              id="sec-f-class-of-beneficiaries"
              label="F3 - If applicable, please state class(es) of beneficiaries (e.g. unit holders, family members of named person, charitable organisation/causes)"
              type="form-input-full-width"
              asterisk=""
              name="beneficiary_class"
              value={secF?.beneficiary_class || ""}
              onChangeFn={onChangeFn}
              formErrors={invFormStatus.errors}
            />
        </div>
    </div>
  )
}

export default BeneficiaryClasses;