import React, {useEffect} from 'react'
import CustomTextField from '../../../../Layout/CustomTextField'
import {replaceAllWhiteSpaces} from '../../../../../../utils/regexFuncs'

function ArsnNumIn({secF, onChangeFn, formErrors}) {

    const onChangeARSN = ({name, value}) => {
        const trimmedVal = replaceAllWhiteSpaces(value)
        onChangeFn({name, value: trimmedVal})
    }

    useEffect(()=>{
        return ()=>{
            onChangeFn({name:'arsn', value:''})
        }
    },[])

  return (
    <div>
        <CustomTextField
            placeholder="Australian Registered Scheme Number"
            id="sec-f-aus-reg-scheme-num"
            label="Australian Registered Scheme Number (ARSN)"
            type="form-input-full-width u-margin-bottom-0"
            asterisk="*"
            name="arsn"
            value={secF?.arsn || ""}
            onChangeFn={onChangeARSN}
            formErrors={formErrors}
        />
    </div>
  )
}

export default ArsnNumIn