import React from 'react';
import CustomTextField from '../../../Layout/CustomTextField';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import { useSelector } from 'react-redux';
import { FAMILY_TRUST_F, CHARITABLE_TRUST_F, TESTAMENTARY_TRUST_F, SELF_MANAGED_SUPERANNUATION_FUND_F, REGISTERED_MANAGED_INVESTMENT_SCHEME_F, UN_REGISTERED_MANAGED_INVESTMENT_SCHEME_F, OTHER_F } from '../../../../../constants';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import OtherTrustIn from './subTrustFundDetails/OtherTrustIn';
import ArsnNumIn from './subTrustFundDetails/ArsnNumIn';
import AbnNum from './subTrustFundDetails/AbnNum';
import SettlorFullName from './subTrustFundDetails/SettlorFullName';

const TrustFundDetails = ({ onFormChange }) => {
    const secF = useSelector((store)=> store.secF);
    const invFormStatus = useSelector((store) => store.invFormStatus)

    const onChangeFn = ({name, value}) =>{
        onFormChange({name, value});
    }

    const onChangeRadioBtn = ({name, value}) =>{ 
        const convrtToNum = parseInt(value);
        onFormChange({name, value:convrtToNum});
    }

  return (
    <div className="section__container">
        <h4 className="section__container--subtitle">F.1 Trust Or Superannuation Fund Detail</h4>
            <div className="section__container--box">
                <CustomTextField
                    placeholder="Full name of Trust or Superannuation fund"
                    id="sec-f-fullname-of-trust"
                    label="Full name of Trust or Superannuation fund"
                    type="form-input-full-width u-margin-bottom-0"
                    asterisk="*"
                    name={"name"}
                    value={secF?.name}
                    onChangeFn={onChangeFn}
                    formErrors={invFormStatus.errors}
                />
            </div>

            <div className="section__container--box">
                <h5>Types of Trust <span>*</span></h5>
                <div>
                    <CustomRadioInput
                        name="type"
                        id="sec-f-family-trust"
                        content="Family Trust"
                        val={FAMILY_TRUST_F}
                        checked={secF?.type === FAMILY_TRUST_F}
                        onChangeFn={onChangeRadioBtn}
                    />
                    <CustomRadioInput
                        name="type"
                        id="sec-f-charitable-trust"
                        content="Charitable Trust"
                        val={CHARITABLE_TRUST_F}
                        checked={secF?.type === CHARITABLE_TRUST_F}
                        onChangeFn={onChangeRadioBtn}
                    />
                    <CustomRadioInput
                        name="type"
                        id="sec-f-testamentary-trust"
                        content="Testamentary Trust"
                        val={TESTAMENTARY_TRUST_F}
                        checked={secF?.type === TESTAMENTARY_TRUST_F}
                        onChangeFn={onChangeRadioBtn}
                    />
                    <CustomRadioInput
                        name="type"
                        id="sec-f-smsf"
                        content="Self-managed superannuation fund (SMSF)"
                        val={SELF_MANAGED_SUPERANNUATION_FUND_F}
                        checked={secF?.type === SELF_MANAGED_SUPERANNUATION_FUND_F}
                        onChangeFn={onChangeRadioBtn}
                    />
                    <CustomRadioInput
                        name="type"
                        id="sec-f-rmis"
                        content="Registered managed investment scheme"
                        val={REGISTERED_MANAGED_INVESTMENT_SCHEME_F}
                        checked={secF?.type === REGISTERED_MANAGED_INVESTMENT_SCHEME_F}
                        onChangeFn={onChangeRadioBtn}
                    />
                    <CustomRadioInput
                        name="type"
                        id="sec-f-urmis"
                        content="Unregistered managed investment scheme"
                        val={UN_REGISTERED_MANAGED_INVESTMENT_SCHEME_F}
                        checked={secF?.type === UN_REGISTERED_MANAGED_INVESTMENT_SCHEME_F}
                        onChangeFn={onChangeRadioBtn}
                    />
                    <CustomRadioInput
                        name="type"
                        id="sec-f-other"
                        content="Other"
                        val={OTHER_F}
                        checked={secF?.type === OTHER_F}
                        onChangeFn={onChangeRadioBtn}
                    />
                    {invFormStatus.errors?.type? <div className="input__desc">
                        {renderErrorTxt(invFormStatus.errors?.type)}
                    </div>: null}
                </div>

                {secF?.type === SELF_MANAGED_SUPERANNUATION_FUND_F 
                || secF?.type === REGISTERED_MANAGED_INVESTMENT_SCHEME_F
                || secF?.type === UN_REGISTERED_MANAGED_INVESTMENT_SCHEME_F?
                    <div className="u-padding-10y">
                        <AbnNum secF={secF} onChangeFn={onChangeFn} formErrors={invFormStatus.errors}/>
                        {secF?.type === REGISTERED_MANAGED_INVESTMENT_SCHEME_F? <ArsnNumIn secF={secF} onChangeFn={onChangeFn} formErrors={invFormStatus.errors}/>: null}
                    </div>: null}

                {secF?.type === OTHER_F? <OtherTrustIn secF={secF} onChangeFn={onChangeFn} formErrors={invFormStatus.errors}/>: null}

                {secF?.type === FAMILY_TRUST_F 
                || secF?.type === CHARITABLE_TRUST_F
                || secF?.type === TESTAMENTARY_TRUST_F? <SettlorFullName onChangeFn={onChangeFn}/>: null}
            </div>

           
        </div>
  )
}

export default TrustFundDetails;