import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import CustomTextField from '../../../../Layout/CustomTextField';

function SettlorFullName({ onChangeFn }) {
    const secF = useSelector((store)=> store.secF);
    const invFormStatus = useSelector((store) => store.invFormStatus);

    useEffect(()=>{
      return ()=>{
        onChangeFn({name:'settlor_full_name', value:''});
      }
    },[])

  return (
    <div className="u-padding-10y">
        <CustomTextField 
            placeholder="Full name of Settlor/s"
            id="sec-f-fullname-settlor"
            label="Full name of Settlor/s* (if applicable)"
            type="form-input-full-width u-margin-bottom-0"
            asterisk=""
            belowText="* The person(s) who settles the initial sum or assets to create the Trust. Not required to be completed if settlor is deceased OR trust is verified using simplified verification e.g. SMSF."
            name="settlor_full_name"
            value={secF?.settlor_full_name}
            onChangeFn={onChangeFn}
            formErrors={invFormStatus.errors}
        />
    </div>
  )
}

export default SettlorFullName