import React from 'react';
import CustomTextField from '../../../Layout/CustomTextField';
import { useSelector } from 'react-redux';

const Trustee = ({secF, onFormChange}) => {
  const invFormStatus = useSelector((store) => store.invFormStatus)

  const onChangeFn = ({name, value}) =>{
    onFormChange({name, value});
  }

  return (
    <div className="section__container">
    <h4 className="section__container--subtitle">F.2 Trustee</h4>

    <div className="section__container--box">
        <CustomTextField
          placeholder="Name of Trustee(s)"
          id="sec-f-name-of-trustee"
          label="Name of Trustee(s)"
          asterisk="*"
          type="form-input-full-width"
          name="trustee_name"
          value={secF?.trustee_name || ""}
          onChangeFn={onChangeFn}
          formErrors={invFormStatus.errors}
        />
    </div>
</div>
  )
}

export default Trustee;