import React from 'react';
import { useSelector } from 'react-redux';
import TrustFundDetails from './SubSections/TrustFundDetails';
import Trustee from './SubSections/Trustee';
import BeneficiaryClasses from './SubSections/BeneficiaryClasses';
import IdentificationDocs from './SubSections/IdentificationDocs';
import { FAMILY_TRUST_F, TESTAMENTARY_TRUST_F } from '../../../../constants';
import Backdrop from '../../Layout/Backdrop';
import { formatAndPrintDocs } from '../../../../utils/validations/helperFunc';

const SectionFContent = ({onFormChange, investorFormId, removeDocFromArr}) => {

  const secF = useSelector((store)=> store.secF);
  const invFormStatus = useSelector((store) => store.invFormStatus);
  
  return (
    <div className="section">
      <h2 className="u-padding-y section__heading" title="Section F: Trust / Superannuation Fund">Section F: Trust / Superannuation Fund</h2>

      <TrustFundDetails onFormChange={onFormChange}/>
      
      <Trustee secF={secF} onFormChange={onFormChange}/> 
      
      {secF?.type === FAMILY_TRUST_F || secF?.type === TESTAMENTARY_TRUST_F? <BeneficiaryClasses secF={secF} onFormChange={onFormChange}/>: null }
      
      <IdentificationDocs investorFormId={investorFormId}/>

      {secF?.documents?.length > 0? formatAndPrintDocs(secF?.documents, removeDocFromArr): null}

    </div>
  )
}

export default SectionFContent;